var _self
export default{
	data(){
		return {
			msg:'',
			imagesrc:'',
			percent:0,
			tempFilePaths:[],				
		}
	},
	onLoad() {
		_self=this
		this.imagesrc=uni.getStorageSync('userinfo').imageSrc
		if(!(this.imagesrc)){
			this.msg='未微信登录，获得不到你的图像'
			console.log(this.msg&&true)
			}
		console.log(this.imagesrc)
	},
	methods:{
		chooseImage(){
			uni.chooseImage({
				count:2,
				sizeType:['processed'],
				success(res) {
					_self.tempFilePaths=res.tempFilePaths
				}
			})
		},
		previewImage(){
			uni.previewImage({
				urls:_self.tempFilePaths,
				
			})
		}
	}
}
